.mat-form-field+.mat-form-field {
  margin-left: 8px;
}

.mat-expansion-panel {
  margin-bottom: 10px;
  box-shadow: 0px 0px 7px -1px rgba(0, 0, 0, 0.08), 0 1px 1px 0 rgba(0, 0, 0, 0),
    0 1px 3px 0 rgba(0, 0, 0, 0) !important;
  border-radius: 10px !important;
}

label {
  display: inline-block;
  color: #3e3e3e;
  font-size: 13px !important;
  font-family: "GothamNarrowBold", sans-serif !important;
  min-width: 124px;
  width: 44px;
  white-space: normal;
  word-break: break-word;
  margin-right: 10px;
  line-height: 14px;
}

#status-toggle .btn-toggle:before {
  content: "off" !important;
  left: -3.5rem !important;
}

#status-toggle .btn-toggle:after {
  content: "On" !important;
  right: -3.5rem !important;
}

#status-toggle .btn-toggle {
  margin: 0 1.5rem !important;
}

textarea {
  resize: none !important;
  height: 90px;
}

.mat-form-field {
  width: 92% !important;
}

.alert-dark {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: 115px;
  margin-top: 16px;
  color: #818181;
  background-color: #f2f2f5;
  border-color: #c6c8ca;
  font-family: "GothamNarrowBold", sans-serif;
  font-size: 17px;
  border-radius: 8px;
}

.alert-dark img {
  cursor: pointer;
}

.alert-dismissible {
  padding-right: 10px;
}

#profile .mat-expansion-panel-body .view-mode .mat-form-field-appearance-outline .mat-form-field-outline-end,
#profile .mat-expansion-panel-body .view-mode .mat-form-field-appearance-outline .mat-form-field-outline-start {
  border: none !important;
  min-width: 5px;
  background: #fff !important;
}

.email-block {
  z-index: 999999;
}

.c-input {
  color: #0072ce;
  font-family: "gothambook", sans-serif;
  font-family: "GothamNarrowBold", sans-serif;
  font-size: 16px;
}

.s-label {
  min-width: 104px !important;
}

.uploadimg {
  position: absolute;
  right: -5px;
  top: 0;
  cursor: pointer;
}

.text {
  font-family: "GothamNarrowBold", sans-serif;
  color: #818181;
}

.roto-outer {
  height: calc(100vh - 114px);
  overflow: auto;
}

.para {
  font-family: "gothambook", sans-serif !important;
  color: #818181;
  font-size: 13px;
}

.para h5 {
  font-size: 15px;
  color: #000;
  font-family: "GothamNarrowBold", sans-serif !important;
}

.mat-datepicker-toggle-default-icon {
  color: #979797 !important;
}

.text.rotor-list {
  font-size: 14px;
  margin-bottom: -1.8rem !important;

}

.text.rotor-list li {
  margin-bottom: 30px;
  list-style-type: none;
}

.rotor-btn {
  width: 200px;
  height: 21.5px;
  color: #818181;
  border-radius: 3px;
  border: solid 0.4px #e09a1e;
  background-color: #f5d148 !important;
}

.text.head {
  font-size: 17px;
}

/* uploadScreen */
.mat-form-field {
  margin: 40px 0;
  width: 500px !important;
}

.mat-toolbar-single-row {
  height: auto !important;
  background: transparent;
}

.mat-toolbar-single-row button {
  width: 200px;
}

.mat-form-field {
  width: 100%;
}

#uploadFile {
  top: 0px;
  left: 0px;
  width: 41.2rem !important;
  z-index: 9;
  opacity: 0;
  height: 100%;
  cursor: pointer;
  position: absolute;
}

.rotor_img {
  margin-top: -15px;
  right: -15px;
  position: relative;
}

.form-fieldCust {
  width: 41.2rem !important;
}

.dateErrorAdj {
  margin-top: -3%;
  margin-left: 4%;
}

.cursor-rem,
.rotor-btn {
  cursor: default !important;
}